@media (max-width: 600px) and (min-width: 200px) {
    .top_nav header {
        background: #c4f67e;
    }
    .bodypart {
        height: unset;
    } 
    .tbl_body .MuiTableCell-root.table_first_row {
        max-width: 180px;
    }
    .mini-games-btn span.MuiButton-label {
        display: block;
        line-height: 24px;
    }
    .user_modal .MuiGrid-grid-xs-6 {
        flex-grow: 0;
        max-width: 100%;
        flex-basis: 100%;
    }
    .mobile-card-tabs .mobile_tab > div > div {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .mheaderm > p > a {
        padding: 2px 10px;
    }
    .bottom_nav button img {
        width: 20px;
    }
    button.Bmenubtn span {
        font-size: 10px;
    }
    .bottom_nav button.Bmenubtn {
        padding: 0;
        min-width: unset;
    }
    .MuiGrid-spacing-xs-2>.MuiGrid-item {
        padding: 0 !important;
    }
    .MuiBottomNavigationAction-label.Mui-selected {
        font-size: 10px !important;
    }
    .m-hide {
        display: none;
    }
    .MuiGrid-root.bodypart.custom-container.grid-row {
        display: block;
    }
    .mini-games-link p {
        margin: 0 10px 0 0;
        font-size: 10px;
        font-style: italic;
    }
    .maintabs button {
        margin: 0 !important;
        padding: 0;
    }
    .MuiGrid-root.bodypart.custom-container.grid-row > div {
        max-width: 95%;
        margin: auto;
    }
    .mini-games-btn::after {
        content: " ";
        position: absolute;
        top: 0;
        bottom: 0px;
        left: 100%;
        margin-top: 0;
        border-width: 13px;
        border-style: solid;
    }
    
}

@media (max-width: 1023px) and (min-width: 601px) { 
    .maintabs button {
        font-size: 16px !important;
        line-height: 30px !important;
    }
    .mini-games-link p {
        margin: 0 20px 0 0;
    }
    .MuiGrid-root.bodypart.custom-container.grid-row {
        display: block;
    }
    .tl-hide{
        display: none;
    }
    .nav_menu .hamburgerMenu {
        display: block !important;
        min-width: auto !important;
        /* background: #f18906 !important;
        box-shadow: 0px 0px 10px 0px #ffffff;
        border-radius: 5px;
        padding: 0; */
        margin-right: 10px;
    }
    .nav_menu .hamburgerMenu .MuiButton-root {
        min-width: 40px;
    }
    .tbl_head th p {
        margin: 0;
    }
    .mobile-card-tabs .mobile_tab > div > div {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
    .MuiGrid-root.bodypart.custom-container.grid-row > div {
        max-width: 95%;
        margin: auto;
    }
    .mini-games-btn::after {
        content: " ";
        position: absolute;
        top: 0;
        bottom: 0px;
        left: 100%;
        margin-top: 0;
        border-width: 12px;
        border-style: solid;
    }
}

@media (max-width: 1260px) and (min-width: 1024px) { 
    .mobile-card-tabs .mobile_tab > div > div {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
@media (max-width: 350px) and (min-width: 200px) { 
    .header-section .mobile_nav a {
        font-size: 10px !important;
        text-decoration: unset !important;
        color: #fff !important;
    }
    .mini-games-link p {
        font-size: 8px;
    }
}
@media (max-width: 600px) and (min-width: 421px) { 
    .header-section .mobile_nav a {
        font-size: 12px !important;
        text-decoration: unset;
        color: #fff;
    }
}
@media (max-width: 767px) and (min-width: 601px) { 
    .marquee {
        display: none;
    }
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .header-section .mobile_nav a {
        font-size: 12px !important;
        text-decoration: unset;
        color: #fff;
    }
    .match-time {
        font-size: 10px;
        color: #878786;
        line-height: initial;
    }
    .match-name {
        font-size: 12px !important;
        color: #3a3a39;
        font-weight: bold;
    }
    .mobile_tab button .MuiGrid-spacing-xs-1 {
        width: 100%;
        margin: auto;
    }
    .app_bar > div .makeStyles-marque-6.MuiTypography-h6,
    .app_bar > div .makeStyles-marque-7.MuiTypography-h6 {
        display: none;
    }
    .logo-outer, .logo-outer img {
        max-width: 130px !important;
        line-height: unset !important;
        width: 100% !important;
        font-size: unset !important;
    }
    /* HEAD PART */
    .makeStyles-title-3{
        width: 100%;
        text-align: center;
    }
    .marquee, .wallet, .wallet_icon, .xs_none, .sidenav, .maintabs{
        display: none !important;
    }

    .top_nav img.user_icon_img{
        width: 30px;
    }

    .mobile_nav{
        background: #2f4f25;        
        width: 100%;
        padding: 0;
        text-align: center;
        display: inline-block !important;
    }

    .mobile_nav a{
        padding: 0 24px;
        vertical-align: middle;
        display: inline-block;
        font-weight: 900;
        color: #000000;
        font-size: 14px;
    }
    
    .mobile_nav a img{
        width: 25px;
        vertical-align: middle;
    }

    .mobile_news{
        display: inline-block;
        width: 100%;
        background: #c4f67e;
        color: #ffffff;
    }

    .mobile_news > .mobile_new_block{
        padding: 0 8px;
        vertical-align: middle;
        display: flex;
        align-items: center;
        overflow: hidden;
        position: relative;
    }

    .mobile_news > .mobile_new_block > div{
        display: inline-block;
        font-weight: bold;
        letter-spacing: .6px;
    }

    .mobile_news > .mobile_new_block .first {
        display: flex;
        align-items: center;
        position: relative;
        z-index: 1;
        font-size: 12px;
        font-style: italic;
        min-width: 70px;
        height: 100%;
        background: #14191b;
        position: absolute;
        left: 0;
    }

    .mobile_news > .mobile_new_block .first img{
        width: 14px;
    }

    .mobile_news > .mobile_new_block .second{
        width: 100%; 
        font-size: 12px;
        text-align: center;
        vertical-align: top;
        line-height: 21px;
        padding-top: 4px;
        margin-left: 4px;
        color: #000;
    }

    .main_tab{
        padding: 0 !important;
    }

    .bottom_nav, .hamburgerMenu, .xs_nav, .main_tab > span > div, .mobile_tab.maintabs{
        display: block !important;
    }

    .xs_tbl_block{
        padding: 8px;
        background: #dfdfdf;
    }

    .xs_tbl_block > div{
        box-shadow: none !important;
    }

    .xs_tbl_block .xs_tbl_first td{
        padding: 4px !important;
    }

    .xs_tbl_block .tbl_body td{
        margin: 10px;
    }

    .xs_tbl_block .tbl_body{
        background: #dfdfdf;
    }

    .xs_tbl_block .tbl_head{
        background: #fafafa;
    }
    
    .xs_tbl_block .tbl_head th{
        color: #000 !important;
    }
    .xs_tbl_block .tbl_head th p {
        margin: 0;
        line-height: 1.4;
    }

    .xs_tbl_block .odd_even_clr .MuiTableCell-root.MuiTableCell-alignRight{
        background: none !important;
        padding: 0;
    }
    
    .xs_tbl_block .odd_even_clr .MuiTableCell-root.MuiTableCell-alignRight p{
        padding: 8px 0;
        margin: 0 auto;
        border-radius: 16px;
        min-width: 40px;
        max-width: 50px;
        font-size: 14px;
    }

    .xs_tbl_block .odd_even_clr .MuiTableCell-root.MuiTableCell-alignRight:nth-child(2n+1) p {
        background: #7cc4f7;
        font-weight: bold;
    }
    
    .xs_tbl_block .odd_even_clr .MuiTableCell-root.MuiTableCell-alignRight:nth-child(2n) p {
        background: #fca4b7;
        font-weight: bold;
    }

    /* BODY PART */

    .bodypart{
        margin-top: 145px !important;
        margin-bottom: 60px !important;
    }

    /* .hamburgerIcon{
        min-width: auto !important;
        background: #f18906 !important;
        box-shadow: 0px 0px 10px 0px #ffffff;
    } */

    .MuiGrid-grid-xs-12{
        background: #dfdfdf;
    }

    /*  */
    .event-single-popup{
        margin-top: 8px !important;
    }

    /* MODAL */

    .xs_modal {
        background: #ffffff;
        width: 98%;
    }

    .modal_btn {
        text-align: center;
    }

    .modal_btn button {
        margin-right: 4px !important;
        margin-left: 8px !important;
    }

    .modal_number {
        text-align: center;
    }

    .modal_number button {
        margin: 2px !important;
        border-radius: 26px;
        background: #1a1a18 !important;
        color: #ffffff;
        box-shadow: 0 0 1px;
        max-width: 50px;
        min-width: 50px;
    }

    .xs_modal .modal_head{
        /* background-image: linear-gradient(to right, #f18906, #f59800, #f8a700, #fab700, #fbc600, #fbc600, #fbc600, #fbc600, #fab700, #f8a700, #f59800, #f18906); */
        background: #c4f67e;
        padding: 0 8px;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
    }

    .xs_modal .modal_body{
        padding: 12px 8px;
        background: #26221c;
    }

    .xs_modal .modal_body input{
        color: #ffffff;
    }
    
    .xs_modal .modal_body label{
        color: #ffffff;
    }

    .xs_modal .modal_body .MuiOutlinedInput-notchedOutline{
        border-color: #999999;
    }

    .xs_modal .modal_footer{
        /* background-image: linear-gradient(to right, #3e3358, #423c51, #333333, #423c51, #3e3358) !important; */
        background: rgb(244,244,244);
        background: -moz-radial-gradient(circle, rgba(244,244,244,1) 5%, rgba(179,179,179,1) 50%, rgba(141,141,140,1) 95%);
        background: -webkit-radial-gradient(circle, rgba(244,244,244,1) 5%, rgba(179,179,179,1) 50%, rgba(141,141,140,1) 95%);
        background: radial-gradient(circle, rgba(244,244,244,1) 5%, rgba(179,179,179,1) 50%, rgba(141,141,140,1) 95%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f4f4f4",endColorstr="#8d8d8c",GradientType=1);
        padding: 8px;
    }

    .xs_modal .modal_footer .modal_btn button{
        /* background-image: linear-gradient(to right, #f18906, #f59800, #f8a700, #fab700, #fbc600, #fbc600, #fbc600, #fbc600, #fab700, #f8a700, #f59800, #f18906) !important; */
        background: #c4f67e !important;
        font-weight: 900 !important;
        min-width: 60px;
        max-width: 60px;
        margin-left: 4px !important;
        padding: 4px;
        text-transform: none;
        margin-top: 4px !important;
        margin-bottom: 0 !important;
    }

    /* ------------ */

    .search_data{
        float: none;
        margin-top: 8px;
        width: 100%;
    }

    .search_filter{
        display: inline-block;
    }

    .datatable_filter{
        padding: 0 16px 12px
    }

    .search_filter > div, .search_filter > input{
        width: 100%;
        margin-bottom: 8px;
    }

    .datatable_check > div{
        justify-content: space-between;
    }

    .datatable_filter.profit_filter > button, .search_filter > button{
        margin-left: 0;
        margin-right: 12px;
    }

    .datatable_filter.profit_filter{
        padding: 8px 12px 8px
    }

    .datatable_filter.profit_filter > div:first-child,
    .datatable_filter.bethistory > div,
    .datatable_filter.livebethistory > div{
        width: 100% !important;
        margin: 0;
        margin-bottom: 8px;
    }

    .datatable_filter.profit_filter > input,
    .datatable_filter.profit_filter > input[type="text"]{
        margin: 0;
        margin-bottom: 8px;
        width: calc(100% - 12px) !important;
    }
}

@media (max-width: 1260px) and (min-width: 1024px) { 
    .txl-grid-2 .MuiGrid-grid-md-4 {
        flex-grow: 0;
        max-width: 50%;
        flex-basis: 50%;
    }
    .tbl_head th p {
        margin: 0;
    }
}