img{
    width: auto;
    max-width: 100%;
}
.logo img {
    max-width: 160px;
    margin: auto;
    display: block;
    width: 100%;
}
span.close {
    right: 10px;
    top: 10px;
    position: absolute;
    color: #fff;
    cursor: pointer;
}

.login-margin{
    margin-bottom: 20px !important;
}

#wrapper {
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    width: 310px;
    overflow: hidden;
    padding: 20px;
    border-radius: 0;
    border: none;
    background: #ffffff;
}

.login-form {
    overflow: hidden;
    display: inline-block;
    border: #C0C0C0 solid 0px;
    border-radius: 10px;
}

.login-form form{
    margin: 20px 0;
}

.login-form input{
    padding: 5px 0;
    min-height: 30px;
    font-size: 14px;
    border-bottom: 2px solid #fff;
    color: #fff;
}

.login-form input:focus,
.login-form input:hover,
.login-form input:active{
    border-bottom: 2px solid #fff !important;
}

.MuiInput-underline:hover:not(.Mui-disabled):before{
    border: 0 !important;
}
.MuiInput-underline::after {
    border-color: #fff !important; 
}

.login-form input::placeholder{
    font-size: 14px;
    color: #fff;
}

.login-form .remember-me .MuiTypography-body1{
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
}

.login-form .remember-me .MuiSvgIcon-root{
    font-size: 16px;
    color: #fff;
}

.login-form .remember-me .PrivateSwitchBase-root-1{
    padding-right: 8px;
}

.login-btn {
    background: #3fab3c !important;
    color: #000 !important;
    font-size: 15px;
    border-radius: 0 !important;
}





/* My CSS For Login popup*/


.login-outer .login-body div#wrapper {
    background: rgba(0,0,0,0.5);
    border: 2px solid #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgb(255 255 255 / 30%);
}
button.login-btn,
.download-btn {
    font-weight: bold;
    border-radius: 5px !important;
    background: #c4f67e !important;
    color: #000 !important;
}
.download-btn span {
    font-weight: bold;
}
.white-txt {
    color:#fff;
    font-weight: bold;
    display: block;
}
.align-center {
    text-align: center;
}
.sp-10 {
    padding: 10px;
}
.login-form label > * {
    display: block;
}
.login-form label {
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}