    width: 24px;
}

.top_nav .marquee div {
    /* border-radius: 50%; */
}

.top_nav .marquee div button:first-child, .top_nav .marquee div {
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

