/* USER MODAL */

.user_modal{
    width: 100%;
    max-width: 700px !important;
    background: #ffffff;
    padding: 24px;
}

.user_modal .MuiTextField-root{
    width: 100%;
    margin-bottom: 16px;
}

/* Main Menu */

.mainmenu>nav {
    background: #000;
    display: inline-block;
    width: 100%;
    color: #ffffff;
}

