/* -------DashBoard Body-------- */

.bodypart {
    margin-top: 120px !important;
    background: #dfdfdf !important;
    min-height: 100vh;
}

.MuiGrid-spacing-xs-2>.MuiGrid-item {
    padding: 0 8px 0 !important;
}

.maintabs {
