/* Side Ads */

.mini-games-bar {
    height: 30px;
    background-color: #1d1638 !important;
    color: #ffffff !important;
    min-height: 10px !important;
}

.mini-games-grid .MuiAccordionDetails-root {
    padding: 10px !important;
}

.mini-games-bar svg {
    color: #ffffff;
}

.mini-games {
    max-width: 100%;
    border-radius: 0 !important;
    background-color: #221f20 !important;
}

.mini-games img {
    /* max-width: 100%;
    width: 100%; */
}

.mini-games-content {
    padding: 0 !important;
    text-align: center;
    text-transform: uppercase;
}

.mini-games-content p {
    font-size: 10px;
}

.mini-games-link {
    display: flex;
    align-items: center;
    background-color: #221f20;
    justify-content: space-between;
}
.MuiCardContent-root.mini-games-content > p {
    line-height: 0.5;
}
.mini-games-link:hover {
    text-decoration: none !important;
}

.mini-games-link p {
    margin: 0 10px 0 0;
    color: #bfff00;
    font-weight: 600;
    font-style: italic;
    font-size: 9px;
}

.mini-games-btn {
    display: flex !important;
    border-radius: 0 !important;
    font-size: 10px !important;
    width: 50%;
    background-color: #50C878 !important;
    margin: 0 0 4px 0 !important;
}

.mini-games-btn::after {
    content: " ";
    position: absolute;
    top: 15%;
    left: 100%;
    margin-top: -5px;
    border-width: 14.5px;
    border-style: solid;
    border-color: transparent transparent transparent #50C878;
}

/* Side Bar Listing */

.side_list {
    background: linear-gradient( 132deg, #eae7d3 8.33%, #f3f0dc 8.33%, #f3f0dc 50%, #eae7d3 50%, #eae7d3 58.33%, #f3f0dc 58.33%, #f3f0dc 100%);
